import * as React from "react";
import { Toolbar, Box } from "@mui/material/";
import MenuList from "./menuList/MenuList";
import { useSelector } from "react-redux";
import { RootState } from "../../../../redux/store";
import MenuDate from "./menuDate/MenuDate";
import DeleteFilterButton from "./deleteFilter/DeleteFilter";
import SearchButton from "./search/Search";

interface EnhancedTableToolbarProps {
  numSelected: number;
}

interface Data {
  id: number;
  text: string;
  rows?: Array<number | string | null> | [Set<string>] | any;
  order?: string;
}

function createData(
  id: number,
  text: string,
  rows?: Array<number | string | null> | [Set<string>] | any,
  order?: string
): Data {
  return {
    id,
    text,
    rows,
    order,
  };
}

const EnhancedTableToolbar = (props: EnhancedTableToolbarProps) => {
  const state = useSelector((state: RootState) => state);

  let arr: Array<string> = [];

  state?.listCallReducer?._allList?.forEach((el: any) => {
    if (el?.source === "") return;
    arr.push(el?.source);
  });

  const rows = [
    createData(
      1,
      state?.listCallReducer?.order === ""
        ? " "
        : state?.listCallReducer?.order,
      ["", ""],
      " "
    ),
    createData(2, " ", ["Yandex", "Google"], " "),
    createData(
      3,
      " ",
      [
        " ",
        " ",
        " ",
        " ",
        " ",
      ],
      " "
    ),
    createData(
      4,
      " ",
      ["Yandex", "Google", "Mail"],
      " "
    ),
    createData(
      5,
      " ",
      [
        "",
        "  ",
        <Box
          sx={{
            color: "#EA1A4F",
            backgroundColor: "#FEE9EF",
            border: "1px solid #EA1A4F",
            borderRadius: "4px",
            p: "6px 8px",
          }}
        >
          
        </Box>,
        <Box
          sx={{
            color: "#122945",
            background: "#D8E4FB",
            border: "1px solid #ADBFDF",
            borderRadius: "4px",
            p: "6px 8px",
          }}
        >
          
        </Box>,
        <Box
          sx={{
            color: "#00A775",
            background: "#DBF8EF",
            border: "1px solid #28A879",
            borderRadius: "4px",
            p: "6px 8px",
          }}
        >
          
        </Box>,
        <Box
          sx={{
            width: "8px",
            height: "8px",
            background: "#EA1A4F",
            borderRadius: "48px",
          }}
        />,
        <Box sx={{ display: "flex", gap: "3px" }}>
          <Box
            sx={{
              width: "8px",
              height: "8px",
              background: "#ADBFDF",
              borderRadius: "48px",
            }}
          />
          <Box
            sx={{
              width: "8px",
              height: "8px",
              background: "#ADBFDF",
              borderRadius: "48px",
            }}
          />
        </Box>,
        <Box sx={{ display: "flex", gap: "3px" }}>
          <Box
            sx={{
              width: "8px",
              height: "8px",
              background: "#28A879",
              borderRadius: "48px",
            }}
          />
          <Box
            sx={{
              width: "8px",
              height: "8px",
              background: "#28A879",
              borderRadius: "48px",
            }}
          />
          <Box
            sx={{
              width: "8px",
              height: "8px",
              background: "#28A879",
              borderRadius: "48px",
            }}
          />
        </Box>,
      ],
      " "
    ),
    createData(
      6,
      " ",
      [
        "",
        "",
        "",
        "",
        "",
        "",
        " ",
      ],
      " "
    ),
  ];

  return (
    <Toolbar
      sx={{
        display: "flex",
        flexDirection: "column",
        alignItems: "flex-end",
        padding: "0 !important",
        gap: "30px",
        marginBottom: "18px",
      }}
    >
      <MenuDate />
      <Box
        sx={{
          display: "grid",
          width: "100%",
          gridTemplateColumns: "auto 1fr",
          gridTemplateAreas: `"deleteB menu"`,
        }}
      >
        <SearchButton />
        <Box
          sx={{
            gridArea: "menu",
            display: "flex",
            alignItems: "center",
            gap: "2%",
            justifyContent: "flex-end",
          }}
        >
          <DeleteFilterButton state={state?.listCallReducer?.order} />
          <MenuList data={rows} state={state} />
        </Box>
      </Box>
    </Toolbar>
  );
};

export default EnhancedTableToolbar;
